body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgba(205, 205, 205, 0.511);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/*  */
.home,
.barcode,
.map,
.search,
.user,
.user-edit,
.chatting-list,
.chatting,
.cart-page,
.store {
  max-width: 390px;
  /* top: 6vh; */
  height: 88vh; /*  100%  */
  margin-left: auto;
  margin-right: auto;
  background-color: #f2f2f2;
}

.store {
  height: 100vh;
}

.item {
  max-width: 390px;
  /* top: 6vh; */
  min-height: 120vh; /*      */
  height: 100%; /*  100%  */
  margin-left: auto;
  margin-right: auto;
  background-color: #f2f2f2;
}

.home,
.user,
.user-edit,
.item,
.store,
.cart-page,
.barcode,
.chatting,
.chatting-list,
.search {
  height: 100vh;
}

.item-image {
  /* margin-top: 20px; *
  /* border-radius: 20px; */
  height: 100%;
  width: 390px;
  object-fit: cover;
}

.item-image-container {
  width: 100%;
  height: 500px;
  overflow: hidden;
}

.map-store-data,
.barcode-item-data {
  position: fixed;
  bottom: 50px;
  max-width: 390px;
  /* overflow: hidden;
  text-align: center; */
}

.ad-image {
  width: 390px;
  height: 100%;
  object-fit: cover;
}

.ad-image-container {
  width: 100%;
  height: 200px;
  overflow: hidden;
  background-size: cover;
}

/*UserEdit page*/
.userEdit-image {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}

.userEdit-image img {
  margin-top: 20px;
  width: 150px;
  height: 150px;
  border-radius: 50%;
}

.edit-icon {
  position: absolute;
  align-items: center;
  justify-content: center;
  display: flex;
  position: relative;
  top: 55px;
  right: 30px;
  cursor: pointer;
  width: 25px;
  height: 25px;
  border-radius: 50%;
  background-color: #e8e8e8;
  color: #333;
}

.userEdit-info-container {
  margin: 30px;
  padding: 20px;
  background-color: #e8e8e8;
  border-radius: 20px;
  box-shadow: 0px 4px 8px 0 rgba(0, 0, 0, 0.2);
  text-align: left;
  font-size: 0.8em; /*     */
  font-weight: bold;
  color: #333;
}

.userEdit-info-container input {
  display: block;
  width: 100%;
  height: 30px;
  border: none;
  outline: none;
  border-radius: 10px;
  margin-bottom: 25px;
  margin-top: 5px;
  margin-right: 10px;
  background-color: #f2f2f2;
}

/* login */
.login-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background: linear-gradient(
    to bottom,
    rgb(83, 27, 27),
    #01172f
  ); /*    */
  color: white; /*     */
}

.login-form {
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 300px;
  background-color: white;
  color: black;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2); /*   */
  padding: 20px;
  border-radius: 20px;
  background-color: #fff; /*     */
}

.login-form label {
  margin-bottom: 10px;
  color: #3a1919; /*      */
}

.login-form input {
  padding: 10px;
  margin-bottom: 10px;
  border: 1px solid #ccc; /*   . */
  border-radius: 10px; /*     . */
  background-color: #f5f5f5; /*     . */
  color: #3a1919; /*     . */
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.2); /*     . */
  transition: background-color 0.2s, border 0.2s; /*    . */
}

.login-form input:focus {
  background-color: white; /*          . */
  border: 1px solid red; /*      */
}

.login-form button {
  font-size: 1rem;
  padding: 10px;
  background-color: rgb(255, 100, 100);
  color: white;
  border: none;
  cursor: pointer;
  border-radius: 10px; /*   . */
  font-weight: bold; /*   . */
}

.login-form button:active {
  background-color: rgb(188, 46, 46);
  transform: scale(0.9);
}

.signup-link {
  margin-top: 10px;
  text-align: center;
}

.signup-link a {
  color: rgb(255, 100, 100);
  text-decoration: none;
  font-weight: bold; /*    . */
}

.signup-link a:active {
  color: darkred;
}

/* Signup.css */
.signup-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  padding: 0 20px;
  background: linear-gradient(
    to bottom,
    rgb(83, 27, 27),
    #01172f
  ); /*    */
  color: white; /*     */
}

.signup-form {
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 300px;
  background-color: white;
  color: black;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2); /*   */
  padding: 20px;
  border-radius: 20px;
  background-color: #fff; /*     */
}

.signup-form label {
  color: #3a1919; /*      */
}

.signup-form input {
  padding: 10px;
  margin-bottom: 10px;
  border: 2px solid #ccc; /*       */
  border-radius: 10px; /*     . */
  background-color: #f5f5f5; /*     . */
  color: #3a1919; /*        . */
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.2); /*     . */
  transition: background-color 0.2s, border 0.2s; /*    . */
}

.signup-form .role-selection input:checked + label {
  font-weight: bold; /*      . */
  color: #2d3ec1;
}

.signup-form input:focus {
  background-color: white; /*          . */
  border: 2px solid red; /*      */
}

.signup-form button {
  font-size: 1rem;
  padding: 10px;
  background-color: rgb(255, 100, 100);
  color: white;
  border: none;
  cursor: pointer;
  border-radius: 10px; /*   . */
  font-weight: bold; /*   . */
}

.signup-form button:active {
  background-color: rgb(188, 46, 46);
  transform: scale(0.9);
}

.login-link {
  margin-top: 10px;
  text-align: center;
}

.login-link a {
  color: rgb(255, 100, 100);
  text-decoration: none;
  font-weight: bold; /*    . */
}

.login-link a:active {
  color: darkred;
}

/*    */
.login-btn {
  padding: 10px 15px;
  margin-bottom: 20px;
}

/* chatting */
/*     */
#chat-container,
.chatting-list-container {
  /* margin: auto; */
  /* max-width: 80%; */
  max-width: 800px;
  padding: 20px;
  /* background: #943636; */
  /* box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1); */
  /* border-radius: 10px;    . */
}

/*     */
#chat-messages,
.chatting-lists {
  height: 80vh; /*  . */
  overflow-y: auto;
  /* border: 1px solid #ccc; */
  /* margin-bottom: 10px; */
  padding: 10px;
  background: #fff; /*   . */
  border-radius: 10px; /*    . */
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

/*     */
#message-input {
  width: 75%; /*  100% . */
  padding: 10px;
  border: none; /*  . */
  border-radius: 10px; /*    . */
  background: #e7e7e7; /*   . */
  height: 20px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

#message-input:focus {
  outline: none;
  background: #d3d3d3; /*      */
}

/*    */
#send-btn {
  width: 19%;
  height: 43px;
  padding: 10px;
  background: rgb(255, 100, 100);
  color: white;
  border: none;
  border-radius: 10px; /*    . */
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

#send-btn:active {
  background: rgb(188, 46, 46);
  transform: scale(0.9);
}

/*    */
.message-bubble {
  padding: 10px 15px;
  border-radius: 20px;
  margin-bottom: 10px;
  display: inline-block;
  max-width: 80%; /*   . */
  word-wrap: break-word; /*       */
}

/*    */
.message-bubble.sent {
  background: #e7f3ff;
  align-self: flex-end;
}

/*    */
.message-bubble.received {
  background: #fff4f4;
  align-self: flex-start;
}

/*     */
#chat-messages {
  display: flex;
  flex-direction: column;
}

/*   */
/* ChattingList */
.chatting-list .chatting-lists,
.seller-chatting-management .chatting-lists {
  height: 85vh;
}

/*     */
.chatting-list .list-item,
.seller-chatting-management .list-item {
  padding: 10px; /*    */
  border-bottom: 1px solid #ccc; /*    */
  display: flex; /*    */
  align-items: center; /*    */
  cursor: pointer; /*     */
  flex-direction: row; /*   */
}

.chatting-list .list-item:active,
.seller-chatting-management .list-item:active {
  background-color: #dcdcdc;
  border-radius: 20px; /*   */
}

.chatting-list .list-item-content,
.seller-chatting-management .list-item-content {
  display: flex;
  align-items: center;
}

/*   */
.chatting-list .round-image,
.seller-chatting-management .round-image {
  width: 75px; /*   */
  height: 75px; /*   */
  border-radius: 50%; /*   */
  object-fit: cover; /*    */
  margin-right: 20px; /*    */
  display: flex; /*    */
}

/*     */
.chatting-list .user-details,
.seller-chatting-management .user-details {
  margin-left: 20px; /*    */
  flex-grow: 1; /*     */
}

/*    */
.chatting-list h2,
.seller-chatting-management h2 {
  margin: 1px; /*    */
  font-size: 1.3em; /*     */
}

/*    */
.chatting-list p,
.seller-chatting-management p {
  margin: 0; /*    */
  color: #666; /*    */
  font-size: 0.8em; /*     */
}

.chatting-list .card-separator,
.seller-chatting-management .card-separator {
  border-top: 1px solid #ccc;
  margin: 20px 0;
}
