/*        */
.nav-wrapper {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  height: 6vh;
  background-color: #fff;
  padding: 1px 0px;
  box-shadow: 0 0px 10px rgba(0, 0, 0, 0.2);
  max-width: 390px;
  margin-left: auto;
  margin-right: auto;
}

/* nav      */
nav {
  overflow: hidden;
  /*      */
  /* border-top: 2px solid rgba(128, 128, 128, 0.623); */
}
/* nav  div     */
.nav-link {
  /* , 5 button  20% width  */
  float: left;
  width: 20%;
  text-align: center;

  /*   */
  height: 50px;
  line-height: 50px;
}

/*       */
.nav-item {
  color: rgba(128, 128, 128, 0.623);
}

/*       */
.active-nav-item {
  color: black;
}
